

body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: #ffffff;
  color: #000000;
}

.app-container {
  width: 100%;
  height: 100%;
}

h1, h2, h3 {
  margin: 10px 0;
}

.input-group {
  margin: 20px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.game-id-input {
  padding: 8px;
  font-size: 16px;
  width: 200px;
  text-align: center;
}

.primary-button {
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 16px;
background: linear-gradient(135deg, rgb(0, 122, 223), rgb(0, 236, 188));
  color: #fff;
  border: none;
  cursor: pointer;
}

.button-start {
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 16px;
background: linear-gradient(135deg, rgb(0, 122, 223), rgb(0, 236, 188));
  color: #fff;
  border: none;
  cursor: pointer;
}

.primary-button:hover {
  opacity: 0.9;
}

.questions-list {
  width: 100%;
  max-width: 600px;
}

.question-item {
  margin-bottom: 20px;
}

.question-text {
  font-weight: bold;
  margin-bottom: 5px;
}

.options-list {
  list-style-type: none;
  padding-left: 20px;
}

.options-list li {
  margin: 3px 0;
}

.feud-list p {
  margin: 3px 0;
}

.slide-container {
  background-image: url('/public/trivia-app.PNG');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  min-height: 100vh;
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.welcome-image {
  width: 70vh;
  padding: 10px;
  color: #fff;
}

.breaktime-image {
  width: 70vh;
  padding: 10px;
  color: #fff;
}

.sec-container {
  background-image: url('/public/Slide3.PNG');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  min-height: 100vh;
  padding: 10px;
  color: #fff;
}

.lastpage-container {
  background-image: url('/public/trivia-app.PNG');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  min-height: 100vh;
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.round1-container {
  background-image: url('/public/Slide4.PNG');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  min-height: 100vh;
  padding: 10px;
  color: #fff;
}

.section-image{
  margin-bottom: auto;
  width: 15%;
  margin: 10px;
  padding: 0;
  line-height: 1;
}

.lastsec-image{
  width: 70vh;
  padding: 10px;
  color: #fff;
}

.sectionNum{
  font-size: 8rem;
  font-weight: bold;
  margin: 0;
  padding: 0;
  line-height: 1;
}

.catName{
  font-size: 3rem;
  font-weight: bold;
  margin: 5rem;
  padding: 0;
  line-height: 1;
}

.dataTitle{
  height: 10px;
  font-size: 1.8rem;
  font-weight: bold;
  color: #be7ebb;
  margin: 0;
  padding: 0;
  line-height: 1;
}

.slide-container h3, h1{
  margin-top: 5rem;
}

.question-text{
  font-size: 16px;
}

.input-answer{
  width: 80%;
  padding: 10px;
  border-radius: 10px;
}

.audio-question button {
  display: inline-block;
}

